/* JetBrainsMono-Bold-Italic - © 2000-2020 JetBrains s.r.o. Developed with drive and IntelliJ IDEA. */
@font-face {
  font-weight: 700;
  font-family: "JetBrains Mono";
  font-style: italic;
  src: local("JetBrains Mono Bold Italic"), local("JetBrainsMono-BoldItalic"),
    url("./JetBrainsMono-Bold-Italic.woff2") format("woff2"),
    url("./JetBrainsMono-Bold-Italic.woff") format("woff");
  font-feature-settings: "calt" 1, "zero" 1, "kern" 1;
  font-display: swap;
}

